import uniSteps from '@/components/uni-steps.vue'
export default {
	components: {
		
		uniSteps
	},
	data() {
		return {
			
			realname: '',
			indentifyCardNum: '',
			opcity: 0.5
		}
	},
	onLoad() {
		//判断用户认证状态，如果已认证提示用户认证状态
		
	},
	onReady() {

	},
	methods: {
		comfirme() {
			//输入姓名身份证号
			console.log(this.indentifyCardNum)
			uni.navigateTo({
				url:"./real-photo?name="+this.realname
			})
			uni.setStorageSync('indentifyCardNum',this.indentifyCardNum)
		},
		change(e) {
			console.log(e.detail.value.length)
			if (e.detail.value.length >=1) {
				this.opcity = 1
			} else {
				this.opcity = 0.5
			}
		}
	}
}
